.contact__container {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 1rem;
}

.contact__options {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.success__message {
    position: absolute;
    padding: .25em;
    color: hsl(var(--clr-white)/.8);
    font-size: 2rem;
    background-color: hsl(var(--clr-white)/.5);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.contact__option {
    color: hsl(var(--clr-white)/.5);
    background: hsl(var(--clr-white)/.1);
    padding: 1rem;
    border-radius: var(--smallBR);
    text-align: center;
    transition: var(--transition-200);
    cursor: pointer;
}

.contact__option:hover {
    box-shadow: 0 0 0 hsl(var(--clr-black)/.0), 0 0 0 hsl(var(--clr-white)/.0),
        inset 3px 3px 3px hsl(var(--clr-black)/.8),
        inset -3px -3px 3px hsl(var(--clr-white)/.05);
}

.contact__type>.contact__icon,
.contact__type>h4 {
    color: hsl(var(--clr-white)/.4);
}

.contact__type>h5 {
    color: hsl(var(--clr-white)/.7);
}

.contact__icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

input, textarea {
    padding: .5em .75em;
    border-radius: .5rem;
    background: hsl(var(--clr-white)/.1);
    /* border: 1px solid var(--color-primary-variant); */
    resize: none;
    color: hsl(var(--clr-green));
    transition: var(--transition-200);
}

textarea {
    height: 100%;
}

input:hover,
input:focus,
textarea:hover, .success__message>textarea:focus {
    box-shadow: 0 0 0 hsl(var(--clr-black)/.0), 0 0 0 hsl(var(--clr-white)/.0),
        inset 3px 3px 3px hsl(var(--clr-black)/.8),
        inset -3px -3px 3px hsl(var(--clr-white)/.05);
}

.sendMessage__btn {
    transition: var(--transition-400);
}

@media screen and (max-width:600px) {
    .contact__container {
        margin-top: 1.25em;
        display: flex;
        flex-direction: column;
    }

    .contact__options {
        display: flex;
    }

    .contact__option {
        padding: .25em;
    }
}